body { font-family: Inter, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial; background: #fafafa; color: #111827; }
button { background: #111827; color: white; border: none; padding: 8px 12px; border-radius: 6px; cursor: pointer; }
button:disabled { opacity: 0.6; }
input, textarea { border: 1px solid #e5e7eb; border-radius: 6px; }
/* app.css */

.styles-grid {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  margin-top: 18px;
  padding: 0 12px;
}

.style-card {
  border: 1px solid #e5e7eb;
  padding: 12px;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
  display: flex;
  flex-direction: column;
}

.style-card img {
  width: 60%;
  height: auto;
  object-fit: cover;
  border-radius: 6px;
}

.style-card h3 {
  margin-top: 8px;
  font-weight: 600;
  color: #111827;
}

.style-card p {
  margin-top: 4px;
  color: #374151;
  flex-grow: 1;
}

.style-card button {
  margin-top: 8px;
  padding: 10px;
  background-color: #f59e0b;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.style-card button:hover {
  background-color: #d97706;
}

/* Responsive tweak for small screens */
@media (max-width: 480px) {
  .styles-grid {
    grid-template-columns: 1fr;
    padding: 0 8px;
  }
  .style-card img {
    height: 140px;
  }
}

